import fs from 'fs';

export default class App {
  constructor(_appElement, _dataStore) {
    this.dataStore = _dataStore;
    this.appElement = _appElement;
    this.template = fs.readFileSync(__dirname + '/appTemplate.html', 'utf8');
  }

  render() {
    this.appElement.innerHTML = this.template;
  }
}
